<template>
  <div class="main rule-group">
    <div class="main-table">
      <div class="table-msg">
        <div class="msg-left">
          <p>查询符合上述条件的数据<span>{{list.totalCount}}</span>条</p>
        </div>
        <div class="msg-right">
            <!--//:on-error="uploadFalse" // :on-success="uploadSuccess"-->
            <el-button  type="button" class="btn-small" @click="add">添加</el-button>
        </div>
      </div>
       <el-table
        :data="list.data"
        style="width: 100%"
        border>
        <el-table-column
          prop="cmemo"
          align="center"
          label="团队">
        </el-table-column>
        <el-table-column
          prop="pmemo"
          align="center"
          label="项目">
        </el-table-column>
        <el-table-column
          prop="mid"
          align="center"
          label="模型">
        </el-table-column>
        <el-table-column
          prop="rank"
          align="center"
          label="比例">
        </el-table-column>
        <el-table-column
          prop="success"
          align="center"
          label="操作">
          <template slot-scope="scope">
            <a href="javascript:void(0)" @click="edit(scope,scope.row)">修改</a>
          </template>
        </el-table-column>
      </el-table>
     <page-table show_size="true" v-bind:cur="search.page" v-bind:total_page="list.totalPage" v-bind:total_count="list.totalCount" @change="change_data"></page-table>
    </div>
      <el-dialog class="dialog-box" :visible.sync="dialog_edit">
        <div class="main-edit">
          <div class="edit-box">
            <div v-show="dialog_type=='add'">
              <div class="info-item">
                <span class="item-left">团队</span>
                <span class="item-right">
                  <el-select style="width:372px;" v-model="corp" @change="changeStaff()">
                      <template v-for="item in teamData">
                          <el-option :label="item.memo" :value="item.id"  :key="item.id">{{item.memo}}</el-option>
                      </template>
                  </el-select>
                </span>
              </div>
              <div class="info-item">
                <span class="item-left">项目</span>
                <span class="item-right">
                  <el-select style="width:372px;" v-model="prjid">
                      <template v-for="item in projData">
                          <el-option :label="item.pmemo" :value="item.id"  :key="item.id">{{item.pmemo}}</el-option>
                      </template>
                  </el-select>
                </span>
              </div>
              <div class="info-item">
                <span class="item-left">模型</span>
                <span class="item-right">
                  <template>
                    <el-select style="width:372px;" v-model="model" placeholder="请选择" filterable :filter-method="dataFilter">
                    <el-option v-for="item in options" :key="item.id" :label="item.memo" :value="item.id" />
                  </el-select>
                  </template>
                </span>
              </div>
            </div>
            <div class="info-item">
              <span class="item-left">比例</span>
              <span class="item-right">
                <input type="Number" class="txt-common" v-model='num' placeholder="请输入比例">
                <span class="msg">请填写比例</span>
              </span>
            </div>
          </div>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="save">保存</el-button>
          <el-button @click="dialog_edit=false">取消</el-button>
        </div>
      </el-dialog>
  </div>
</template>

<script>
  import {get_datacmemo} from '@/api/index'
  import pager from '../../components/pager'
  import ajax from '@/config/ajax'
  import Qs from 'qs'
  export default {
    name: 'getProTab',
    data () {
      return {
        search:{
          size: 20, // 每页数量
          page: 1 // 页码
        },
        list: {
          data: [], // 数据集
          totalPage: 0, // 总页数
          totalCount: 0, // 总条数
        },
        corp:"",       //外呼团队
        prjid: "",      //外呼项目
        num: '8000',
        model: '', //模型
        modelvalue: '',  //模型id
        teamData: [],         //外呼团队字典数组
        projData: [],  //外呼项目字典树组
        modelData: [], //数据批次字典数组
        options: [],
        csvS: [],
        isCorpContainAll: true,  //外呼团队是否选中全选 
        dialog_edit: false,    //修改弹框
        dialog_type: ''     //弹框类型 修改、添加
      }
    },
    created() {
      this.teamData = JSON.parse(localStorage.getItem("cropid"));
      this.modelData = JSON.parse(localStorage.getItem("model"));
      this.options = this.modelData;
      this.teamData = this.teamData.slice(1);
      this.model = this.options[0].id;
      this.corp = this.teamData[0].id;
      this.getProjData();
      this.getJobLogList();
    },
    watch: {
      'model': {
        handler: function() {
          this.csvS = [];//这是定义好的用于存放下拉提醒框中数据的数组
            var len = this.modelData.length;//csvList是页面初始化时得到的初始全部数据
            var arr = [];
            for (var i = 0; i < len; i++) {//根据输入框中inputName的值进行模糊匹配
              if(this.model == ''){
                arr.push(this.modelData[i]);
              }else{
                if (this.modelData[i].memo.indexOf(this.model) >= 0) {
                  arr.push(this.modelData[i]);//符合条件的值都放入arr中
                }
              }
              
            }
            //el-autocomplete元素要求数组内是对象，且有value属性，此处做一个格式调整
            for (var i = 0; i < arr.length; i++) {
              var obj = { value: "" , id: ''};
              obj.value = arr[i].memo;
              obj.id = arr[i].id;
              this.csvS.push(obj);
            }
        }
      }
    },
    methods: {
      change_data (index, size) {
        this.list.page = index;
        this.list.size = size;
        this.getJobLogList()
      },
      //修改
      edit() {
        this.dialog_edit = true;
        this.dialog_type = 'edit';
      },
      add() {
        this.dialog_edit = true;
        this.dialog_type = 'add';
      },
      save() {
        // console.log(this.dialog_type);
        // var params = {
        //   mid:this.modelvalue,
        //   cid:this.corpid,
        //   num: this.num,
        //   area: this.cityvalue,
        //   prjid: this.prjid
        // }
        // console.log(params);
        // return ajax.request({
        //   method:'post',
        //   url:'provide',
        //   headers: {
        //     'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        //   },
        //   transformRequest: [function(params) {      //在请求之前对data传参进行格式转换
        //     params = Qs.stringify(params)
        //     return params
        //   }],
        //   data:params
        // }).then((res)=>{
        //   console.log(res);
        //   if(res.code == '200'){
        //     this.$message({
        //       type: 'success',
        //       message: '提交成功'
        //     })
        //   }
        // }).catch((error)=>{
        //   console.log(error)
        // })
      },
      changeStaff(){
        this.getProjData();
      },
      querySearch(queryString, cb) {
        var csvS = this.csvS;
        cb(csvS);
      },
      handleSelect(item){
        this.modelvalue = item.id;
      },
      // 获取初始化列表
      getJobLogList () {
        var params = {
          page: this.search.page,
          size: this.search.size
        }
        return ajax.request({
          method: 'get',
          url: 'rate',
          headers: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
          },
          transformRequest: [function(params) {      //在请求之前对data传参进行格式转换
            params = Qs.stringify(params)
            return params
          }],
          data:params
        }).then((res) => {
          // console.log(res);
        }).catch(error => {
          console.log(error);
        })
      },
      dataFilter(val){
        if(this.model){
          this.options = this.modelData.filter((item) => {
            if (!!~item.memo.indexOf(val)) {
              return true
            }
          })
        }else{
          this.options = this.modelData;
        }
      },
      getProjData(){
        // console.log(this.corp);
        var params = {
          cmemo: this.corp
        }
        get_datacmemo(this.corp,"pmemo/all").then((res) => {
          if(res.code == '200'){
            // console.log(res.data)
            this.projData = res.data.list;
            // console.log(this.projData)
            this.prjid = this.projData[0].id;
          }
        }).catch(error => {
          console.log(error);
        })
      }
    },
    components: {
      'page-table': pager
    }
  }
</script>

<style scoped>
  @import '../../assets/css/pages/main.css';
  .rule-group{
    padding-left: 15px;
  }
  .rule-group .main-table table{
    margin-bottom: 30px;
  }
  .el-select-dropdown__wrap ul li{
    padding-left:20px!important;
    display:block;
  }
</style>



